<template>
  <div class="login-wrap">
    <div class="ms-title">用户登录</div>
    <div class="ms-login">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="0px"
        class="demo-ruleForm"
      >
        <el-form-item prop="loginName">
          <el-input v-model="ruleForm.loginName" placeholder="loginName"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            type="password"
            placeholder="password"
            v-model="ruleForm.password"
            @keyup.enter.native="submitForm('ruleForm')"
          ></el-input>
        </el-form-item>

        <div class="login-btn">
          <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data: function() {
    return {
      checked: false,
      ruleForm: {
        loginName: "",
        password: "",
      },
      rules: {
        loginName: [
          { required: true, message: "请输入用户名", trigger: "blur" }
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          //localStorage.setItem("loginName", this.ruleForm.loginName);
          //服务端校登录信息
          this.$ajax({
              method: 'post',
              url: 'userLogin',
              data: this.ruleForm
          }).then(res => {
              if(res.data.status == 200){
                  localStorage.setItem("ms_loginName", this.ruleForm.loginName);
                  this.$router.push("/");
              }
              else{
                  alert("用户名或密码错误")
              }
            })
            .catch(err => {
              alert(err);
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>
